import React, { Component } from 'react';
import ListItems from "./ListItems";
import { library } from '@fortawesome/fontawesome-svg-core'
import { faTrash } from '@fortawesome/free-solid-svg-icons'
library.add(faTrash);
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            items: [],
            currentItem: {
                text: '',
                key: '',
            }
        }
    }
    handleInput = (e) => {
        this.setState({
            currentItem: {
                text: e.target.value,
                key: Date.now()
            }
        })
    }

    addItem = (event) => {
        event.preventDefault()
        const newItem = this.state.currentItem;
        if (newItem.text !== "") {
            const newItems = [...this.state.items, newItem]
            this.setState({
                items: newItems,
                currentItem: {
                    text: '',
                    key: ''
                }
            })
        }
    }
    deleteItem = (key) => {
        const filteredItems = this.state.items.filter(item =>
            item.key !== key);
        console.log(filteredItems)
        this.setState({
            items: filteredItems
        })
    }
    render() {
        return (
            <div className="App">
                <form id="to-do-form">
                    <input type='text' value={this.state.currentItem.text} onChange={this.handleInput} placeholder="Enter Text" />
                    <button type="submit" className="disableBlur" onClick={this.addItem}>Add</button>
                </form>
                <ListItems
                    items={this.state.items}
                    deleteItem={this.deleteItem}
                />

            </div>
        );
    }
}

export default App;